import styled from "styled-components";

export const Container = styled.div`
  background-image: linear-gradient(
    90deg,
    ${(props) => `
      ${props.startColor} 0%,
      ${props.startColor} calc(${props.value * props.multiplier}% * 0.08),
      ${props.endColor} ${props.value * props.multiplier}%
    `}
  );
`;
