import styled from "styled-components";

export const Container = styled("div")`
  display: grid;
  grid-template-columns: 1fr;

  ${(props) => {
    return props.divisions.map(
      (pair) => `
        @media (min-width: ${pair[0]}) {
          grid-template-columns: repeat(${pair[1]}, 1fr);
        }      
      `
    );
  }};
`;
