import styled from "styled-components";

const transitionDuration = 0.14;
const slant = 8;

const disabledStyle = `
  color: #e9e9e9;
  background: #564931;
  opacity: 0.9;
  cursor: default;
`;

export const Container = styled.a`
  display: block;
  box-sizing: border-box;
  /*
  width: fit-content;
  height: fit-content;
  padding: 22px 44px;
  */

  font-weight: 500;
  font-size: 20px;
  white-space: nowrap;

  text-decoration: none;
  text-shadow: none;

  transition-duration: ${transitionDuration}s;

  user-select: none;

  align-items: center;

  cursor: ${(props) => (props.href || props.onClick ? "pointer" : "default")};

  ${(props) =>
    props.disabled
      ? disabledStyle
      : `
  :hover {
/*     box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3); */
  }

  `}
`;

export const TextContainer = styled.span`
  display: inline-block;
`;
