import { Container, Header, TextContainer, ThinLine } from "./style.js";

export default function SectionHeader({ children, mainHeight, ...attribs }) {
  return (
    <Container {...attribs}>
      <ThinLine />
      <Header width={"80%"} height={mainHeight}>
        <TextContainer>{children}</TextContainer>
      </Header>
    </Container>
  );
}
