import { Container, Title, TitleBg } from "./style.js";

export default function TitleHeader({ children, width, height, ...attribs }) {
  return (
    <Container height={height ? height : "90px"} {...attribs}>
      <TitleBg width={width ? width : "100%"}></TitleBg>
      <Title>{children}</Title>

      {/*         <div style={{marginLeft:'calc(100px - 100%)', ...titleStyle}}></div> */}
      {/*         <div style={{width:'calc(100% - 30px)', margin:'0 0 0 auto', ...titleStyle}}></div>
        <div style={titleStyle}>{children}</div>
        <div style={titleStyle}></div> */}
    </Container>
  );
}
