import {
  ComingSoon,
  Container,
  ContentContainer,
  Icon,
  TextContainer,
} from "./style.js";

export default function ButtonSlanted({
  children,
  disabled,
  comingSoon,
  onClick,
  icon,
  className,
  ...attribs
}) {
  const callOnClick = (e) => {
    if (!disabled && onClick) onClick(e);
  };

  return (
    <Container
      className={className}
      onClick={callOnClick}
      disabled={disabled}
      {...attribs}
    >
      <ContentContainer>
        {icon && <Icon src={icon}></Icon>}
        <TextContainer>{children}</TextContainer>
      </ContentContainer>
      {comingSoon && <ComingSoon>Coming Soon</ComingSoon>}
    </Container>
  );
}
