import {
  AboutContainer,
  AvatarImg,
  Container,
  TextContainer,
  TitleContainer,
} from "./style.js";

export default function ProfileCardIF({ children, avatar, title, ...attribs }) {
  return (
    <Container {...attribs}>
      <AvatarImg src={avatar}></AvatarImg>
      <TextContainer>
        <TitleContainer>{title}</TitleContainer>
        <AboutContainer>{children}</AboutContainer>
      </TextContainer>
    </Container>
  );
}
