import { Container, TextContainer } from "./style.js";

export default function Button({
  children,
  disabled,
  onClick,
  className,
  ...attribs
}) {
  const callOnClick = (e) => {
    if (!disabled && onClick) onClick(e);
  };

  return (
    <Container
      className={className}
      onClick={onClick ? callOnClick : null}
      disabled={disabled}
      {...attribs}
    >
      <TextContainer>{children}</TextContainer>
    </Container>
  );
}
