import imgRoadmapBullet from "../../images/roadmap bullet.svg";
import {
  BottomContainer,
  BulletPoint,
  BulletDate,
  ConnectionBar,
  RoadmapContainer,
  TextContainer,
} from "./style.js";

export function Container({ children, ...attribs }) {
  return (
    <RoadmapContainer {...attribs}>
      {children}
    </RoadmapContainer>
  );
}

export function Bullet({ children, direction, date, ...attribs }) {
  return (
    <>
      <BulletPoint>
        <BulletDate>{date}</BulletDate>
        <ConnectionBar direction={direction}/>
      </BulletPoint>
      <TextContainer>{children}</TextContainer>
    </>
  );
}
