import styled from "styled-components";

const transitionDuration = 0.14;
const slant = 8;

const disabledStyle = `
/*  color: rgb(247 203 164);*/
  background:  rgb(153 90 35);
  opacity: 0.9;
  cursor: default;

  > * {
    opacity: 0.5;
  }

  :hover {
    box-shadow: none;
  }
`;

export const Container = styled.a`
  display: flex;
  box-sizing: border-box;

  font-weight: 500;
  font-size: 20px;
  white-space: nowrap;
  text-decoration: none;
  text-shadow: none;

  transition-duration: ${transitionDuration}s;

  user-select: none;

  align-items: center;

  cursor: pointer;

  transform: skewX(-${slant}deg);

  ${(props) =>
    props.disabled
      ? disabledStyle
      : `
  :hover {
/*     box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3); */
  }

  `}
`;

export const ContentContainer = styled.div`
  display: flex;
  justify-content: center;
  transform: skewX(${slant}deg);
`;

export const Icon = styled.img`
  height: 1.5em;
  margin-right: 8px;
`;

export const TextContainer = styled.div`
  align-self: center;
`;

export const ComingSoon = styled.div`
  position: absolute;
  opacity: 1;
  right: 0;
  bottom: -10px;
  font-size: 0.8em;
`;
