import { Container } from "./style.js";

export default function RowByAutoFit({
  children,
  className,
  minWidth,
  maxWidth,
  ...attribs
}) {
  return (
    <Container
      minWidth={minWidth ? minWidth : "320px"}
      maxWidth={maxWidth ? maxWidth : "1fr"}
      className={className}
      {...attribs}
    >
      {children}
    </Container>
  );
}
