import { Container } from "./style.js";

export default function ProgressionBar({
  children,
  startColor,
  endColor,
  value,
  multiplier,
  ...attribs
}) {
  return (
    <Container
      startColor={startColor}
      endColor={endColor}
      value={value}
      multiplier={multiplier ? multiplier : 1}
      {...attribs}
    >
      {children}
    </Container>
  );
}
