import styled from "styled-components";
import imgRoadmapBullet from "../../images/roadmap bullet 90deg.svg";

export const RoadmapContainer = styled.div`
  display: grid;
  grid-template-columns: minmax(100px, 1fr) 1fr;
  box-sizing: border-box;
  margin: auto;
 `;

export const BulletPoint = styled.div`
  position: relative;
  aspect-ratio: 428.36/220.32;
  min-width: 100px;
  max-width: 400px;
  padding-bottom: 120px;
  background-image: url("${imgRoadmapBullet}");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transform: scaleX(-1);
`;

export const BulletDate = styled.div`
  position: absolute;
  width: 40%;
  right: 0;
  top: calc(50% - 12px);
  text-align: center;
  transform: scaleX(-1);
`;

export const ConnectionBar = styled.div`
  position: absolute;
  width: 3px;
  right: 96%;
  background: white;

  ${props=>{
    let height;
    let top;
    switch(props.direction && props.direction.toLowerCase()) {
      case 'down':
        height = 50
        top = 50
        break;
      case 'up':
        height = 50
        top = 0
        break;
      default:
        height = 100
        top = 0
        break;
    }
    return `
      height: ${height}%;
      top: ${top}%;        
    `
  }}

`;

export const TextContainer = styled.div`
  padding-left: 30px;
  align-self: center;
  text-align: left;
  box-sizing: border-box;
`;
